<template>
  <div>
    <h2 v-if="title && todos.length > 0">{{ title }}</h2>
    <ul>
      <TodoItem
        v-for="(todo, i) of todos"
        :todo="todo"
        :key="todo.id"
        :index="i"
        @remove-todo="removeTodo"
      />
    </ul>
  </div>
</template>

<script>
import TodoItem from '@/components/TodoItem.vue';

export default {
  props: ['todos', 'title'],
  components: {
    TodoItem,
  },
  methods: {
    removeTodo(id) {
      this.$emit('remove-todo', id);
    },
  },
};
</script>

<style scoped>
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
</style>
