<template>
  <li>
    <span :class="{done: todo.completed}">
      <label>
        <input type="checkbox"
          @change="todo.completed = !todo.completed">

        <strong>
          {{ index + 1 }}
        </strong>

        {{ todo.title | uppercase }}
      </label>
    </span>

    <button class="remove-btn"
      @click="$emit('remove-todo', todo.id)">
        &#10060;
    </button>
  </li>
</template>

<script>
export default {
  props: {
    todo: {
      type: Object,
      required: true,
    },
    index: Number,
  },
  filters: {
    uppercase(value) {
      return value.toUpperCase();
    },
  },
};
</script>

<style scoped>
  li {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
    padding: .5rem 2rem;
    border: 1px solid #ccc;
  }

  input {
    margin-right: 1rem;
  }

  .remove-btn {
    font-weight: bold;
    border-radius: 50%;
    color: #fff;
  }

  .done {
    text-decoration: line-through;
  }
</style>
